import React from 'react';
import PropTypes from 'prop-types';

import endPoints from '../../constants/endpoints';
import { fetchToken, fetchBot } from '../../constants/helpers';

class DeletePost extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isDeleting: false,
            message: null,
            error: null,
        };

        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();

        const { match: { params: { postId, postType } } } = this.props;
        this.deletePost(postId, postType);
    }

    async deletePost(postId, postType) {
        if (!!postId && !!postType) {
            this.setState({ isDeleting: true });

            const endPointX = (postType === 'gif') ? endPoints.gifs : endPoints.articles;
            const fetchConfig = {
                method: 'DELETE',
                mode: 'cors',
                headers: {
                    'Content-Type': 'application/json',
                    token: fetchToken(),
                },
            };

            try {
                const result = await fetchBot(`${endPointX}/${postId}`, fetchConfig);
                this.setState({ isDeleting: false, error: null, message: result.data.message });
            } catch (e) {
                this.setState({ isDeleting: false, message: null, error: e.message || e.error.message });
            }
        }
    }

    render() {
        const { match: { params: { postType } } } = this.props;
        const { isDeleting, message, error } = this.state;

        return (
            <>
                {message && <span className="message success">{message}</span>}
                {error && <span className="message error">{error}</span>}

                <div>
                    <div>
                        Are you sure you want to delete the
                        <strong>{postType}</strong>
                        {' '}
                        Post?
                    </div>

                    <button type="submit" onClick={this.handleSubmit} disabled={isDeleting}>
                        {isDeleting ? 'Deleting' : 'Continue'}
                    </button>
                </div>
            </>
        );
    }
}

DeletePost.propTypes = {
    match: PropTypes.objectOf.isRequired,
};

export default DeletePost;
